<template>
  <div>
    <el-row style="margin-bottom:20px;">
      <el-card shadow="always">
        <span class="content-title">焦点TAB管理</span>
        <el-button
          type="success"
          @click="openAddDialog()"
        >新增</el-button>
      </el-card>
    </el-row>
    <el-table
      border
      :data="tableData"
    >
      <el-table-column
        prop="index"
        label="序号"
        align="center"
        width="60"
      >
      </el-table-column>
      <el-table-column
        prop="title"
        label="标题"
        align="center"
      >
      </el-table-column>
      <el-table-column
        prop="status_name"
        label="状态"
        align="center"
        width="100"
      >
      </el-table-column>
      <el-table-column
        prop="release_time"
        label="发布时间"
        align="center"
        width="160"
      >
      </el-table-column>
      <el-table-column
        prop="update_user"
        label="修改者"
        align="center"
        width="110"
      >
      </el-table-column>
      <el-table-column
        prop="update_time"
        label="修改时间"
        align="center"
        width="160"
      >
      </el-table-column>
      <el-table-column
        label="操作"
        align="center"
        width="170"
      >
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="handleEdit(scope.$index, scope.row)"
          >编辑</el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"
          >删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="block pager">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page.sync="currentPage"
        :page-sizes="[15, 20, 30]"
        :page-size="page_size"
        layout="sizes, prev, pager, next"
        :total="total"
      >
      </el-pagination>
    </div>
    <el-dialog
      title="新增"
      :visible.sync="dialogVisible"
      width="60%"
      top="5vh"
      :append-to-body='true'
      :before-close="handleClose"
      :closeOnClickModal='false'
    >
      <div class="el-dialog-news">
        <el-form
          label-position="top"
          :rules="rules"
          ref="formLabelAlign"
          label-width="80px"
          :model="formLabelAlign"
        >
          <el-form-item
            label="标题"
            prop="title"
          >
            <el-input v-model="formLabelAlign.title"></el-input>
          </el-form-item>
          <el-form-item
            label="icon图 图片建议尺寸:600×400"
            prop="ico_name"
          >
            <el-col :span="20">
              <el-input
                v-model="formLabelAlign.ico_name"
                :disabled="true"
              ></el-input>
            </el-col>
            <el-upload
              class="upload-demo"
              action="core/file/upload"
              multiple
              :limit="1"
              :before-upload="beforeUpload"
              :show-file-list='false'
            >
              <el-button
                size="medium"
                type="primary"
              >点击上传</el-button>
            </el-upload>
          </el-form-item>
          <el-form-item
            label="摘要"
            prop="summary"
            style="margin-bottom:0;"
          >
          <el-input
            type="textarea"
            v-model="formLabelAlign.summary"
            :autosize="{ minRows: 10, maxRows: 33}"
            placeholder="请输入内容">
          </el-input>
          </el-form-item>
          <el-form-item label="状态">
            <el-radio-group v-model="formLabelAlign.status">
              <el-radio :label="0">不显示</el-radio>
              <el-radio :label="1">显示</el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="排序">
            <el-select
              v-model="formLabelAlign.sort_no"
              placeholder="请选择"
              style="width:100%"
            >
              <el-option
                v-for="item in sort_no_list"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item
            label="发布时间"
            prop="release_time"
          >
            <el-date-picker
              v-model="formLabelAlign.release_time"
              type="datetime"
              value-format="yyyy-MM-dd HH:mm:ss"
              placeholder="选择日期"
              default-time="01:01:01"
            >
            </el-date-picker>
          </el-form-item>
          <el-form-item label="源链接">
            <el-input v-model="formLabelAlign.link"></el-input>
          </el-form-item>
        </el-form>
      </div>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="handleClose">取 消</el-button>
        <el-button
          type="primary"
          @click="addNewsInfo('formLabelAlign')"
        >确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import { authToken, authName } from "@/config";
import { storageHelper, getCurTime, utcTimeChange } from "@/utils";
export default {
  data() {
    return {
      auth_name: "",
      selectClass: "el-icon-arrow-down",
      content_show: false,
      //表格
      tableData: [],
      currentPage: 1,
      page_size: 15,
      total: 0,
      //弹出框
      dialogVisible: false,
      //表单
      formLabelAlign: {
        title: "",
        summary: "",
        icon: "",
        ico_name: "",
        sort_no: 1,
        status: 0,
        link: ""
      },
      rules: {
        title: [{ required: true, message: "请输入标题", trigger: "blur" }],
      },
      token:'',
      sort_no_list: [
        {
          value: '1',
          label: '1'
        },
        {
          value: '2',
          label: '2'
        },
      ],
    };
  },
  mounted() {
    this.auth_name = storageHelper.getItem(authName, "healthcare");
    this.token = storageHelper.getItem(authToken, "healthcare");
    this.getSolutionList("1", "15");
    this.getdDictionaryInfo();
  },
  methods: {
    handleSizeChange(val) {
      this.getSolutionList(this.currentPage, val);
    },
    handleCurrentChange(val) {
      this.getSolutionList(val, this.page_size);
    },
    handleClose() {
      var that = this;
      this.$confirm('是否保存？',{
        showClose:false
      })
      .then(() => {
        that.addNewsInfo('formLabelAlign');
      })
      .catch(() => {this.dialogVisible = false});
    },
    openAddDialog() {
      this.formLabelAlign = {
        title: "",
        summary: "",
        icon: "",
        ico_name: "",
        sort_no: 1,
        status: 0,
        link: ""
      };
      this.dialogVisible = true;
    },
    beforeUpload(file) {
      var that = this;
      var fd = new window.FormData();
      fd.append("file", file);
      axios
        .post("core/file/upload", fd, {
          headers: {
            "Content-Type": "multipart/form-data",
            token: this.token
          }
        })
        .then(function(res) {
          if (res.code == 0) {
            var url = res.data.url;
            that.formLabelAlign.ico_name = file.name;
            that.formLabelAlign.icon = res.data.code;
            that.$message({
              type: "success",
              message: "上传成功！"
            });
          }
          //console.log(res);
        });
      return false; // 返回false不会自动上传
    },
    //编辑
    handleEdit(index, row) {
      this.dialogVisible = true;
      this.formLabelAlign = {
        tab_id: row.tab_id,
        title: row.title,
        summary: row.summary,
        icon: row.icon,
        ico_name: row.ico_name,
        sort_no: row.sort_no,
        status: row.status,
        link: row.link
      };
    },
    //删除
    handleDelete(index, row) {
      var that = this;
      this.$confirm("是否确认删除该条信息?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        var params = { tab_id: row.tab_id };
        that.submitSolutionInfo("content/tab/delete", params, "删除成功!");
      }).catch(() => {});
    },
    //编辑框提交按钮
    addNewsInfo(formType) {
      var that = this;
      this.$refs[formType].validate(valid => {
        if (valid) {
          var method = "";
          var info = "";
          if (that.formLabelAlign.hasOwnProperty("tab_id")) {
            method = "content/tab/update";
            info = "修改成功!";
          } else {
            method = "content/tab/create";
            info = "添加成功!";
          }
          that.submitSolutionInfo(method, that.formLabelAlign, info);
        } else {
          return false;
        }
      });
    },
    getSolutionList(page_index, page_size) {
      var params = {
        page_index: page_index,
        page_size: page_size
      };
      var that = this;
      axios.get("content/tab/pageList", { params }).then(
        function(response) {
          var code = response.code;
          if (0 == code) {
            var array = [];
            var rdata = response.data;
            var count = rdata.count;
            var page_index = rdata.page_index;
            var page_size = rdata.page_size;
            that.currentPage = page_index;
            that.page_size = page_size;
            that.total = count;
            var des_num = (page_index - 1) * page_size;
            var json = rdata.rows;
            for (var i = 0; i < json.length; i++) {
              var status = json[i].status;
              var icon_src = "";
              var ico_name = "";
              if (null != json[i].icon_info) {
                icon_src = json[i].icon_info.src;
                ico_name = json[i].icon_info.name;
              }
              json[i].create_time = utcTimeChange(json[i].create_time);
              json[i].release_time = utcTimeChange(json[i].release_time);
              json[i].update_time = utcTimeChange(json[i].update_time);
              var status_name = "显示";
              if (status == 0) {
                status_name = "未显示";
              }
              json[i].index = des_num + i + 1;
              json[i].status_name = status_name;
              json[i].icon_src = icon_src;
              json[i].ico_name = ico_name;
              array.push(json[i]);
            }
            that.tableData = array;
          } else {
            //console.log(code);
          }
        },
        function(error) {
          //console.log(error);
        }
      );
    },
    submitSolutionInfo(method, params, info) {
      var that = this;
      axios.post(method, params).then(
        function(response) {
          var code = response.code;
          if (0 == code) {
            that.$message({
              type: "success",
              message: info
            });
            that.dialogVisible = false;
            that.getSolutionList(that.currentPage, that.page_size);
          } else {
            that.$message({
              type: "success",
              message: "操作失败:" + response.message
            });
          }
        },
        function(error) {}
      );
    },
    getdDictionaryInfo() {
      var that = this;
      var params = { code: "tab_type" };
      axios.get("core/dictionary/list", { params }).then(
        function(response) {
          var code = response.code;
          if (0 == code) {
            that.tab_type_list = response.data;
          } else {
          }
        },
        function(error) {}
      );
    }
  }
};
</script>

<style scoped>
.content-title {
  margin-right: 20px;
}
.advanced-box {
  padding: 20px 10px;
  margin-top: 15px;
  background-color: #f7f7f7;
}
.pager {
  float: right;
  padding: 10px 0;
}
.el-dialog__header {
  border-bottom: 1px solid #ccc;
}
.el-dialog-news {
  height: 530px !important;
  padding-right: 15px;
  overflow-y: scroll;
}
.el-dialog__footer {
  border-top: 1px solid #ccc;
}
.el-form-item__label {
  padding-bottom: 0 !important;
}
</style>


